<template>
  <div class="list-row">
    <div class="list-row d-flex justify-content-center mb-3">
      <h1 class="my-4">Clientes</h1>
    </div>
  </div>

  <div class="col-md-12">
    <a
      class="
        list-group-item list-group-item-action list-group-item-light
        d-flex
        justify-content-between
      "
      v-for="(cliente, index) in clientes"
      :key="index"
      @click="push(cliente.id)"
    >
      <span class="my-4">{{ cliente.nombre }}</span>
      <span class="my-4">A pagar ${{ cliente.saldo }}</span>
    </a>
  </div>
</template>

<script>
import { useLoadClientes } from "@/firebase";

export default {
  setup() {
    const clientes = useLoadClientes();

    return { clientes };
  },
  methods: {
    push(id) {
      this.$router.push("/clientes/" + id);
    },
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>